.gpt3__cta {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: var(--gradient-bar);
  border: 1px solid #000000;
  box-sizing: border-box;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 1rem;
  padding: 2rem;
  margin: 4rem;
}
.gpt3__cta-content p {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 12px;
  line-height: 30px;

  /* or 250% */

  color: #0e0e0e;
}
.gpt3__cta-content h3 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 24px;
  line-height: 45px;

  /* or 187% */

  color: #000000;
}
.gpt3__cta-button {
  display: flex;
  justify-content: center;
  align-items: center;

  margin-left: 2rem;
}
.gpt3__cta-button button {
  padding: 0.5rem 1rem;
  cursor: pointer;
  background: #000000;
  border-radius: 40px;
  outline: none;
  border: none;

  font-family: var(--font-family);
  font-weight: bold;
  font-size: 18px;
  line-height: 30px;

  color: #ffffff;
  min-width: 150px;
}

@media screen and (max-width:650px){
    .gpt3__cta{
        flex-direction: column;
        
    }
    .gpt3__cta-button {
        margin-top: 2rem;
        margin-left: 0;
    }
}
@media screen and (max-width:550px){
    .gpt3__cta{
        margin:4rem 2rem;
    }
    .gpt3__cta-content h3{
        font-size: 18px;
        line-height: 32px;
    }
    .gpt3__cta-button button{
        font-size: 14px;
        line-height: 28px;
    }
}