:root {
  --font-family: 'Manrope', sans-serif;

  --gradient-text: linear-gradient(89.97deg, #AE67FA 1.84%, #F49867 102.67%);
  --gradient-bar: linear-gradient(103.22deg, #AE67FA -13.86%, #F49867 99.55%);
  
  --color-bg: #040C18;
  --color-footer : #031B34;
  --color-blog: #042c54;
  --color-text: #81AFDD;
  --color-subtext: #FF8A71;
}
.gpt3__blog {
  display: flex;
  flex-direction: column;
}
.gpt3__blog-heading {
  width: 100%;
  text-align: left;
  margin-bottom: 5rem;
}
.gpt3__blog-heading h1 {
  font-family: var(--font-family);
  font-style: normal;
  font-weight: 800;
  font-size: 62px;
  line-height: 75px;

  letter-spacing: -0.04em;
}
.gpt3__blog-container{
    display: flex;
    flex-direction: row;
}
.gpt3__blog-container_groupA{
    flex: 0.75 1;
    margin-right: 2rem;
}
.gpt3__blog-container_groupB{
    flex: 1 1;
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-gap: 2rem;
}

@media screen and (max-width:990px){
    .gpt3__blog-container{
        flex-direction: column-reverse;
    }
    .gpt3__blog-container_groupA{
        flex: 1 1;
        margin-top: 2rem;
    }
    .gpt3__blog-container_groupA .gpt3__blog-container_article{
        width: 48%;
    } 
    .gpt3__blog-container_groupA .gpt3__blog-container_article-image{
      height: 250px;
    } 
}
@media screen and (max-width:700px){
    .gpt3__blog-container_groupB{
        grid-template-columns: repeat(1,1fr);
    }
    .gpt3__blog-container_groupA .gpt3__blog-container_article{
        width: 100%;
    } 
    .gpt3__blog-container_groupA{
        margin-right: 0px;
    }
    .gpt3__blog-heading h1{
        font-size: 48px;
    }
}

